import React, { useState } from 'react';
import { Button, Form } from 'semantic-ui-react';

export default function Checkout() {
  const [name, setName] = useState('');

  handleSubmit = async (event) => {
    event.preventDefault();

    const response = await fetch('/customer', { method: 'POST' });
    const customer = await response.json();

    console.log(`Customer: ${JSON.stringify(customer)}`);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Field>
        <label>Name</label>
        <input placeholder='First Last' value={name} onChange={(e) => setName(e.tartget.value)} />
      </Form.Field>
      {/* <Form.Field>
        <label>Email</label>
        <input placeholder='test@test.com' value={this.state.email} onChange={this.handleChange} />
      </Form.Field> */}
      <Button primary type='submit' value='submit'>
        Submit
      </Button>
    </Form>
  );
}
